  line-height: 21px;
  color: #555555;
  display: block;
  margin-bottom: 17px;
  padding-bottom: 20px;
  border-bottom: 1px solid #DEDEDE; }

.card-blog__info {
  display: flex;
  align-items: center;
  padding-bottom: 14px; }
  .card-blog__info time {
    font-size: 14px;
    line-height: 14px;
    color: #777777;
    font-family: 'Graphik LCG', sans-serif; }

.card-blog__body {
  display: flex;
  flex-direction: column; }

.card-blog__hashtag {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #777777; }
  .card-blog__hashtag:hover {
    text-decoration: none;
    color: #777777; }
  .card-blog__hashtag span {
    color: #3DACE3;
    font-weight: 600;
    margin-right: 6px; }

.card-blog__info-counter {
  margin-left: 21px;
  display: flex;
  align-items: center; }
  .card-blog__info-counter span {
    font-size: 14px;
    line-height: 14px;
    color: #777777;
    font-family: 'Graphik LCG', sans-serif;
    margin-left: 8px; }

.card-blog__info-time {
  font-size: 14px;
  line-height: 14px;
  color: #4759A4;
  font-family: 'Graphik LCG', sans-serif; }

@media (max-width: 767.98px) {
  .card-blog__title {
    font-size: 16px;
    line-height: 20px; }
  .card-blog__text {
    font-size: 14px; }
  .card-blog__info time,
  .card-blog__info-time,
  .card-blog__info-counter span {
    font-size: 12px; } }

.pagination-wrapper {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; }

.pagination {
  flex-wrap: wrap; }
  .pagination .page-item {
    background: #FFFFFF;
    border: 1px solid #4E64B4;
    border-radius: 4px;
    overflow: hidden;
    margin-left: 3px;
    margin-right: 3px;
    margin-bottom: 4px; }
    .pagination .page-item:hover .page-link {
      transition: 0.2s;
      background: #4E64B4;
      border-color: #4E64B4;
      color: #FFFFFF; }
    .pagination .page-item.active .page-link {
      background: #4E64B4;
      border-color: #4E64B4; }
  .pagination .page-link {
    font-weight: bold;
    font-size: 14px;
    line-height: 17px;
    color: #4E64B4;
    transition: 0.2s; }

html, body {
  font-family: 'Montserrat', sans-serif; }

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding-top: 81px; }

body.active {
  overflow: hidden; }

body {
  position: relative; }

.wrapper .active {
  color: red; }

.tab_item {
  display: none; }

.tab_item:first-child {
  display: flex; }

section {
  padding-top: 61px;
  padding-bottom: 73px; }

.plus-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.wrapper-line {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 0; }

.wrapper-line__line {
  position: absolute;
  top: -200px;
  bottom: 0;
  width: 1px;
  height: 200%;
  opacity: 0.1;
  border-left: 1px solid #3DACE3;
  z-index: -1; }

.wrapper-line__line-1 {
  left: 25%; }

.wrapper-line__line-2 {
  left: 50%; }

.wrapper-line__line-3 {
  left: 75%; }

@media (min-width: 1200px) {
  .container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1360px; }
  .content-container {
    max-width: 1141px; } }

@media (max-width: 1199.98px) {
  .wrapper-line {
    display: none; } }

@media (max-width: 767.98px) {
  .app {
    padding-top: 51px; } }
