.text-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 58px;
  padding-bottom: 10px;
  position: relative;
  height: 100%;
  padding-left: 20px;
  padding-right: 20px;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: -24px;
    background: url("../img/plus-icon.svg") no-repeat center;
    background-size: 100% auto;
    display: block;
    width: 16px;
    height: 16px;
  }

  &:after {
    content: '';
    position: absolute;
    bottom: 40px;
    right: -24px;
    background: url("../img/plus-icon.svg") no-repeat center;
    background-size: 100% auto;
    display: block;
    width: 16px;
    height: 16px;
  }
}

.text-card__icon {
  margin-bottom: 35px;
}

.text-card__icon-circle {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #F5F7FB;
  border-radius: 50%;
  width: 84px;
  height: 84px;
}


.text-card__title {
  align-self: flex-start;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.1px;
  color: #333333;
  display: block;
  margin-bottom: 13px;
}

.text-card__content {
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.1px;
  color: #777777;
}

.text-card__body {
  display: flex;
  flex-direction: column;
}

@media (max-width: 767.98px) {
  .text-card {
    padding-top: 12px;
    padding-bottom: 0;
    flex-direction: row;
    align-items: flex-start;
  }

  .text-card__title {
    align-self: flex-start !important;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.1px;
    margin-bottom: 10px;
  }

  .text-card__content {
    font-size: 14px;
  }

  .text-card__icon-circle {
    width: 60px;
    height: 60px;

    i {
      max-width: 60%;
    }
  }

  .text-card__icon {
    margin-right: 27px;
  }
}

@media (max-width: 1199.98px) {
  .text-card:before,
  .text-card:after {
    display: none;
  }
}
